import React, { ChangeEvent, useState } from 'react'


export const SectionTest = () => {

    const [text, seText] = useState('')

    const onChangeHadler = (e: ChangeEvent<HTMLInputElement>) => {
        seText(e.currentTarget.value)
    }

    const sendToWhatsApp = () => {
        const textNew = encodeURIComponent(text)
        let url = `https://web.whatsapp.com/send?phone=${79505437572}&text=${textNew}&source=&data=`
        window.open(url)
        seText('')
    }

    return (
        <div>
            <input type="text" value={text} onChange={onChangeHadler}></input>
            <button onClick={sendToWhatsApp}></button>
        </div>
    )
}
